.block{
    width: 100px;
    height: 100px;
}
.button{
    display: inline;
    width: 100%;
    height: 100%;
    font-size: 50px;
    outline: none;
}
.xcolor{
    background:green;
    color: white;
}
.ocolor{
    background:blue;
    color: white;
}

.win{
    background: greenyellow;
    position: relative;
}
.win::after{
    content:"";
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    width:50%;
    height: 50%;
    background: rgba(226, 28, 28, 0.3);
    z-index: 1;
    animation:win 0.5s linear infinite;
}
@keyframes win{
    100%{
        width: 100%;
        height: 100%;
    }
}